<template>
  <div id="app" v-bind:style="{'height':'100%'}">
      
      <div class="div-main">
      
      <div class="danmu-txt">    
          <ul>
            <li v-for="value in arr" v-bind:class="['animate']" v-ccc="this" >{{value}}</li>
          </ul>
      </div>
      

        <div class="danmu-inp">
           <input type="text" v-model="inpmsg" name="" value="" placeholder="">
           <input type="button" name="" @click.stop="sendMsg()"  value="发布">  
        </div>
     
      </div>
  </div>
</template>

<script>
export default {

   directives: {
    ccc: {
    bind:function(obj){
    var _this=obj;
    var top=Math.round(Math.random()*95+1);
    var color='#'+(function(h){
    return new Array(7-h.length).join("0")+h
    })((Math.random()*0x1000000<<0).toString(16));
    _this.style.top=top+"%";
    _this.style.color=color;
    }
  }
},

  name: 'app',
  data () {
    return {
      arr:["xia","dasd",1121,"151","dasddasd"],
      inpmsg:"",
      arr1: ["xia","dasd",1121,"151","dasddasd","dasdas","xia","dasd",1121,"151","xia","dasd","151","dasddasd","dasddasd","dasdas","xia","dasddasd","xia","dasd","151","dasddasd"]
    }
  },
  methods:{
    sendMsg:function(ev){
      if(this.inpmsg==""){
           alert("请填写信息");
           return;
      }
       this.arr.push(this.inpmsg);
       this.inpmsg="";
    }
  },
  mounted() {
    (function(z,y){
     var c=setInterval(function(){
            var index = Math.floor((Math.random()*y.length));
            z.push(y[index])
          },1000)
    })(this.arr,this.arr1)
    
  }

}

</script>

<style>

* { -webkit-text-size-adjust: none; -moz-text-size-adjust: none; -ms-text-size-adjust: none; -webkit-tap-highlight-color: transparent; padding: 0; margin: 0; text-decoration: none; }
*:not(input, textarea) { -webkit-touch-callout: inherit; -webkit-user-select: auto; }
body { width: 100%; font-family: Hiragino Sans GB, Arial, Helvetica, "microsoft yahei", sans-serif; font-size: 14px; color: #000; -webkit-touch-callout: inherit; -webkit-user-select: auto; -webkit-box-align: stretch; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); -webkit-box-orient: vertical; background-color: #f4f7f8; color: #444444; }
body, html { height: 100%; }
a { text-decoration: none; -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
a:hover { text-decoration: none; }
img { vertical-align: middle; }
button, input, select, textarea { font-size: 100%; margin: 0; padding: 0; outline: none; }
textarea, input { resize: none; outline: none; -webkit-appearance: none; }
ul, ol, li { list-style: none; }
h1, h2, h3, h4, h5, h6, b { font-size: 100%; font-weight: normal; }
em, i { font-style: normal; }
.loading-div { position: absolute; z-index: 9999; top: 0; right: 0; bottom: 0; left: 0; display: block; text-align: center; background: #fff; }
.loading-div span { position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); }
.loading-div span img { width: 30px; }
.loading-div span i { font-size: 14px; display: block; margin-top: 5px; color: #45567d; }
.div-main { position: relative; box-sizing: border-box; width: 100%; min-width: 320px; max-width: 750px; min-height: 100%; margin: 0 auto;  background: url(./assets/6631482281583828547.jpg);    overflow: hidden; background-size: 100% 100%;
    background-repeat: no-repeat; }
.fl { float: left; }
.fr { float: right; }
.db { display: block; }
.tc { text-align: center; }
.pos-r { position: relative; }
.pos-a { position: absolute; }
.clearfix:after, .clearfix:before { display: table; clear: both; content: ''; }
.clearfix:after { clear: both; }
.danmu-txt{     position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 45px;
    overflow: hidden;}
.danmu-txt ul{    height: 100%;
    position: relative;}
.danmu-txt ul li{    position: absolute;
    background: rgba(255, 255, 255, 0.2);
    padding: 3px 10px;
    border-radius: 22px;    right: -100%;  opacity: 0;}

.animate{
  -webkit-animation: goup 10s both;
  animation: goup 10s both;
}

.danmu-inp {       height: 44px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0px;}
.danmu-inp input[type="text"]{ width: 75%; height: 41px; padding: 10px; font-size: 16px; border: none; border-bottom: 1px solid #ccc; }
.danmu-inp input[type="button"]{    font-size: 16px;
    color: #fff;
    padding: 10px;
    width: 25%;
    border: none;
    background: #5bb7ff;}

@-webkit-keyframes goup {
    0% { right: -100% ;   opacity: 0; }
    10% { right: 0%   ;  opacity: 1; }
    100% { right: 150%  ;   opacity: 1; }
}

@keyframes goup {
    0% { right: -100% ;   opacity: 0; }
    10% { right: 0%  ;   opacity: 1; }
    100% { right: 150% ;    opacity: 1; }
}

</style>



